module.exports = "\r\n<div class=\"container\">\r\n\r\n    <form #form=\"ngForm\" >\r\n    <ul>\r\n\r\n        <li>\r\n            <span>Title</span>\r\n            <input class=\"meta-info\" type=\"text\" name=\"title\" ngModel> <br />\r\n            <p>\r\n            To help other DALN users find your literacy narrative, please provide a brief title for your literacy narrative. (Required)\r\n            </p>\r\n        </li>\r\n\r\n        <li>\r\n          <span>Description</span>\r\n          <input class=\"meta-info\" type=\"text\" name=\"description\" ngModel> <br />\r\n          <p>\r\n            To help other DALN users find your literacy narrative, please describe your literacy narrative briefly in this box (Optional).\r\n          </p>\r\n        </li>\r\n\r\n        <li>\r\n          <span>Date Created</span>\r\n          <select>\r\n            <option value=''>--Select Month--</option>\r\n            <option selected value='1'>Janaury</option>\r\n            <option value='2'>February</option>\r\n            <option value='3'>March</option>\r\n            <option value='4'>April</option>\r\n            <option value='5'>May</option>\r\n            <option value='6'>June</option>\r\n            <option value='7'>July</option>\r\n            <option value='8'>August</option>\r\n            <option value='9'>September</option>\r\n            <option value='10'>October</option>\r\n            <option value='11'>November</option>\r\n            <option value='12'>December</option>\r\n          </select>\r\n          <select >\r\n              <option>1</option>\r\n              <option>2</option>\r\n              <option>3</option>\r\n              <option>4</option>\r\n              <option>5</option>\r\n              <option>6</option>\r\n              <option>7</option>\r\n              <option>8</option>\r\n              <option>9</option>\r\n              <option>10</option>\r\n              <option>11</option>\r\n              <option>12</option>\r\n              <option>13</option>\r\n              <option>14</option>\r\n              <option>15</option>\r\n              <option>16</option>\r\n              <option>17</option>\r\n              <option>18</option>\r\n              <option>19</option>\r\n              <option>20</option>\r\n              <option>21</option>\r\n              <option>22</option>\r\n              <option>23</option>\r\n              <option>24</option>\r\n              <option>25</option>\r\n              <option>26</option>\r\n              <option>27</option>\r\n              <option>28</option>\r\n              <option>29</option>\r\n              <option>30</option>\r\n              <option>31</option>\r\n        </select>\r\n        <select>\r\n         <option>2009</option>\r\n         <option>2010</option>\r\n         <option>2011</option>\r\n         <option>2012</option>\r\n         <option>2013</option>\r\n         <option>2014</option>\r\n         <option>2015</option>\r\n         <option>2016</option>\r\n         <option>2017</option>\r\n         <option>2018</option>\r\n        </select>  <br />\r\n          <p>\r\n            Please provide the date on which you created your literacy narrative -- not necessarily the date on which you are filling out this form. (Optional)\r\n          </p>\r\n        </li>\r\n\r\n        <p>\r\n            NOTE: If you would like to assign a Creative Commons (CC) license to your narrative, thereby retaining ownership while specifying the uses that others may make of your narrative, please visit the Creative Commons site at http://creativecommons.org/choose/, choose a license, then place a license notification to this field, including the name of the CC license (e.g., “My Narrative’s Title” by Lewis Ulman is licensed under a Creative Commons Attribution-NonCommercial-NoDerivs 3.0 Unported License).\r\n\r\n            If you wish to contribute your narrative under a Deed of Gift, thereby transferring ownership of your narrative to the DALN, which will establish its own guidelines for usage, you will have an opportunity to do so at the end of the submission process. (Optional)\r\n        </p>\r\n\r\n        <li>\r\n          <span>Subject Keyword</span>\r\n          <input class=\"meta-info\" type=\"text\" name=\"subject\" ngModel> <br />\r\n          <p>\r\n            To help other DALN users find your literacy narrative, please enter appropriate subject keywords or phrases. You may enter as many as you like, but you should enter only one keyword or phrase at a time, then click \"Add More\" to enter additional keywords. (Optional)\r\n          </p>\r\n        </li>\r\n\r\n        <li>\r\n          <span>Decades covered</span>\r\n          <input class=\"meta-info\" type=\"text\" name=\"decades\"> <br />\r\n          <p>\r\n              Please indicate the decades referred to in your literacy narrative. You can choose as many as necessary, but you may need to hold down the Shift or CTRL key to select multiple choices. (Optional)\r\n          </p>\r\n        </li>\r\n\r\n        <li>\r\n          <span>Nationality</span>\r\n          <input class=\"meta-info\" type=\"text\" name=\"nation\"> <br />\r\n          <p>\r\n              To help other DALN users find narratives by people of a particular nationality, please list your nationality/nationalities during the period referred to in your narrative. (Optional)\r\n          </p>\r\n        </li>\r\n\r\n        <li>\r\n          <span>Region</span>\r\n          <input class=\"meta-info\" type=\"text\" name=\"region\"> <br />\r\n          <p>\r\n            To help other DALN users find narratives from particular regions (e.g., New England, Rocky Mountains, Great Plains) please list the region(s) in which the events described in your narrative took place. (Optional)\r\n          </p>\r\n        </li>\r\n\r\n        <li>\r\n          <span>State or Province</span>\r\n          <input class=\"meta-info\" type=\"text\" name=\"state\"> <br />\r\n          <p>\r\n            To help other DALN users find narratives from your state or province, please list the state(s) or province(s) in which the events described in your narrative took place. (Optional)\r\n          </p>\r\n        </li>\r\n\r\n        <li>\r\n          <span>Other Geographical Information</span>\r\n          <input class=\"meta-info\" type=\"text\" name=\"state\"> <br />\r\n          <p>\r\n            Please provide any further description of the places referred to in your narrative that you consider important (e.g., urban, suburban, rural, inner-city Detroit). (Optional)\r\n          </p>\r\n        </li>\r\n\r\n        <li>\r\n          <span>Language</span>\r\n          <input class=\"meta-info\" type=\"text\" name=\"state\"> <br />\r\n          <p>\r\n            Please enter the language(s) used or referred to in your literacy narrative. (Optional)\r\n          </p>\r\n        </li>\r\n\r\n\r\n    </ul>\r\n\r\n    </form>\r\n\r\n    <pre>\r\n        {{form.value | json}}\r\n   </pre>\r\n    <button type=\"submit\" (click)=\"next()\">Next Step</button>\r\n\r\n\r\n</div>\r\n"

