module.exports = "<div class=\"container\">\r\n\r\n    <form action=\"https://tg1vruzadg.execute-api.us-west-1.amazonaws.com/production/upload2\" method=\"post\" enctype=\"multipart/form-data\">\r\n  <p><input type=\"text\" name=\"text1\" value=\"text default\">\r\n  <p><input type=\"text\" name=\"text2\" value=\"a&#x03C9;b\">\r\n  <p><input type=\"file\" name=\"file1\">\r\n  <p><input type=\"file\" name=\"file2\">\r\n  <p><input type=\"file\" name=\"file3\">\r\n  <p><button type=\"submit\">Submit</button>\r\n  </form>\r\n\r\n    <input type=\"file\"  />\r\n\r\n    <p>\r\n        Please enter the full path of the file on your computer corresponding to your item. If you click \"Browse...\", a new window will allow you to select the file from your computer.\r\n\r\n       We recommend that you use the following file formats for compatibility with our system and broader accessiblity by end users, who typically must open the files on their own computers --\r\n\r\n       TEXT: Microsoft Word (.doc or .rtf); plain text (.txt); or Adobe Acrobat (.pdf)\r\n       IMAGES: JPEG (.jpg or .jpeg), GIF (.gif), or PNG (.png)\r\n       AUDIO: MP3 or QuickTime (.mov)\r\n       VIDEO: QuickTime (.mov)\r\n       WEB: HTML (.htm or .html)\r\n\r\n       If your file (particularly audio or video) is larger than 35 MB, we recommend that you split it into two or more files, with no single file larger than about 35 MB so that visitors to the site will be able to download your file(s) more conveniently.\r\n\r\n    </p>\r\n\r\n    <li>\r\n      <span>File Description</span>\r\n      <input class=\"meta-info\" type=\"text\" name=\"file\"> <br />\r\n      <p>\r\n        Optionally, provide a brief description of the file, for example \"Main article\", or \"Experiment data readings\".\r\n      </p>\r\n    </li>\r\n\r\n    <button type=\"submit\" (click)=\"next()\">Next Step</button>\r\n\r\n\r\n</div>\r\n"

