module.exports = "\r\n<div class=\"container\">\r\n\r\n    <form #form=\"ngForm\">\r\n    <ul>\r\n\r\n        <li>\r\n            <span>Author</span>\r\n            <input class=\"meta-info\" type=\"text\" name=\"contributorAuthorFirstName\">\r\n            <input class=\"meta-info\" type=\"text\" name=\"contributorAuthorLastName\"> <br />\r\n            <p>\r\n            If you wish, enter the name of the author of this literacy narrative (you can click \"Add More\" to enter multiple names for a collaborative narrative). (Optional)\r\n            </p>\r\n        </li>\r\n\r\n        <li>\r\n          <span>Year-of-Birth</span>\r\n          <input class=\"meta-info\" type=\"text\" name=\"creatorYearOfBirth\" ngModel> <br />\r\n          <p>\r\n              To help other DALN users find narratives by people of a particular age group, please provide your year of birth (or years of birth for collaborative entries), using four digits. (Optional)\r\n          </p>\r\n        </li>\r\n\r\n        <li>\r\n          <span>Gender</span>\r\n          <select>\r\n              <option value=\"male\">Male</option>\r\n              <option value=\"female\">Female</option>\r\n          </select> <br />\r\n          <p>\r\n             To help other DALN users find narratives by people of a particular gender or sexual orientation, please describe your gender (for example, male, female, transgender) and/or sexual orientation (for example: gay, bisexual, heterosexual). (Optional)\r\n          </p>\r\n        </li>\r\n\r\n    </ul>\r\n\r\n    </form>\r\n\r\n    <pre>\r\n        {{form.value | json}}\r\n   </pre>\r\n\r\n    <button type=\"submit\" (click)=\"next()\">Next Step</button>\r\n\r\n\r\n</div>\r\n"

